@import url('https://fonts.googleapis.com/css2?family=Changa+One:ital@0;1&display=swap');
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: white;
  font-family: 'Changa One', cursive;
}

body {
  background-image: url('../src/images/background.png');
  animation: backdrop  25s linear 5s infinite;
  animation-direction: alternate-reverse;
  transition: 25s;
  overflow-x: hidden;
}

main {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
  text-align: center;
}

h1 {
  font-size: 4.5rem;
  margin-top: 30px;
  margin-bottom: 30px;
}

h2 {
  font-size: 4.5rem;
  margin-top: 30px;
  margin-bottom: 90px;
}

/* .title {
  font-size: 1.5rem;
  margin-bottom: 75px;
  margin-top: 75px;
} */

.planets {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
  width: 100%;
}


.container-planets {
  position: relative;
  width: 125px;
  height: 125px;
  transform-style: preserve-3d;
  animation: rotate 90s linear infinite;
  margin-top: 200px;
  margin-bottom: 500px;
}

@keyframes rotate {
  0% {
    transform: perspective(1000px) rotateY(0deg);
  }

  100% {
    transform: perspective(1000px) rotateY(360deg)
  }
}

@keyframes backdrop {
  0% {
    background-position: 0 50%;
  }

  100% {
    background-position: 100% 0;
  }
}

.container-planets div {
  position: absolute;
  top: 10px;
  left: 0;
  width: 100%;
  height: 100%;
  transform-origin: center;
  transform-style: preserve-3d;
}

.container-planets div img {
  position: absolute;
  top: 35px;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: 2s;
}

.container-planets div:hover img {
  transform: translateY(15px)
}

.div-mission-title {
  width: 100%;
}

.section-card {
  display: flex;
  justify-content: center;
  width: 90vw;
  flex-wrap: wrap;
  align-items: center;
  text-align: center;
}

.container-missions-card {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
  background: rgba(255, 255, 255, 0.541);
  opacity: 0.8;
  border-radius: 30px;
  box-shadow: 0px 4px 14px rgba(0, 0, 0, 0.9);
}

.mission-card {
  background: rgba(24, 26, 37, 0.5);
  mix-blend-mode: hard-light;
  border: 1px solid #ffffff;
  border-radius: 17px;
  width: 350px;
  padding: 10px 15px;
  margin: 25px;
}

.mission-card:hover {
  transform: translateY(-10px);
  transition: 2s ease-in-out;
}

hr {
  margin-bottom: 5px;
}

.mission-card p {
  margin-top: 5px;
  margin-bottom: 10px;
} 

.mission-name {
  text-align: start;
  margin-top: 5px;
  margin-bottom: 5px;
}

.div-year-country {
  display: flex;
}

.div-year-country p {
  margin-right: 15px;
}

.mission-card-informations {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  height: 50px;
}

.name {
  color: blue;
}

::-webkit-scrollbar {
  width: 7px;
  height: 8px;
  background-color: #aaa; 
}

::-webkit-scrollbar-thumb {
  background: #201e1e;
  border-radius: 5px;
}

@media screen And (min-width: 800px)  {
  .mission-card {
    width: 450px;
  }
}